.App {
  text-align: center;
  font-family: "Goudy Old Style", Garamond, "Big Caslon", "Times New Roman", serif;
  font-size: 24px;
  font-style: normal;
  font-variant: normal;
  font-weight: 500;
  line-height: 26.4px;
}

h1{
  font-size:55px;
  letter-spacing: 3px;
}

h5{
  letter-spacing: 1.5px;
}

h4{
  letter-spacing: 1.5px;
}



.front-page{
  height:80%;
}

.image {
  width: 100%;
  height: auto;
}

.photoGallery {
  width: 100%;
  height: auto;
  float:left;
  margin-top:5%;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin-left:auto;
  margin-right:auto;
  height: auto;
  width: 100%;
  opacity: 0;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  background-color: rgba(0,0,0,0.65);
}

.hover-img:hover .overlay {
  opacity: 1;
    cursor: pointer
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

.nopadding{
  padding:0 !important;
}

#links {
  cursor: pointer;
  text-decoration:none;
  color:black;
}

#links:hover{
  color:gray;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;

}


.loader {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #000000;
  border-radius: 50%;
  -webkit-animation: 1.1s infinite linear;
          animation: 1.1s infinite linear;
}

@-webkit-keyframes {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



